<template>
    <div class="icons">
      <swiper :options="swiperOption">
        <swiper-slide v-for="(page,index) of pages"  :key="index">
         <div class="icon" v-for="item of page" :key="item.id">
          <div class="icon-img">
            <img  class="icon-img-content" :src="item.imgUrl"/>
          </div>
          <p class="icon-desc">{{ item.desc}}</p>
         </div>
        </swiper-slide>
      </swiper>
    </div>
</template>

<script>
    export default {
        name: "HomeIcons",
        props:{
          list:Array
        },
       data(){
          return{
            swiperOption:{
              autoplay:false
            }
          }
       },
        computed:{
          pages(){//分页函数
            const pages=[]
            this.list.forEach((item,index) => {
              const page = Math.floor( index / 8 )
              if(!pages[page]){
                pages[page]=[]
              }
              pages[page].push(item)
            })
            return pages
          }
        }
    }
</script>

<style scoped lang="stylus">
  @import '~styles/varibles.styl'
  @import "~styles/mixins.styl"
  .icons >>> .swiper-container //扩大左滑右滑的范围
   height:0
   padding-bottom:50%

   .icon
     position:relative
     overflow:hidden
     float:left
     width:25%
     height:0
     padding-bottom:25%

     .icon-img
       position:absolute
       top:0
       left:0
       right:0
       bottom:.44rem
       box-sizing:border-box
       padding:.1rem

       .icon-img-content
         height:100%
         display:block
         margin:0 auto
     .icon-desc
       position:absolute
       left:0
       right:0
       bottom:0
       height:.44rem
       line-height:.44rem
       color:$darkTextColor
       text-align:center
       ellipse()

</style>



