<template>
    <div>
      <div class="recommend-title">
        热销推荐
      </div>
      <ul>
        <router-link tag="li" class="item border-bottom" v-for="item of list" :key="item.id" :to=" '/detail/ ' + item.id"><!--将router-link渲染成li标签而非a标签，这样文字就不会变化-->
          <img class="item-img"  :src="item.imgUrl">
          <div class="item-info">
            <p class="item-title">{{ item.title}}</p>
            <p class="item-desc">{{item.desc}}</p>
            <button class="item-button">查看详情</button>
          </div>
        </router-link>
      </ul>
    </div>
</template>

<script>
    export default {
      name: "HomeRecommend",
      props:{
        list:Array
      }
    }
</script>

<style scoped lang="stylus">
  @import "~styles/mixins.styl"
  .recommend-title
    margin-top .2rem
    line-height .4rem
    background #ccc
    text-indent .2rem//首行文本缩进
  .item
    height 1.9rem
    display flex
    .item-img
      width 1.7rem
      height 1.7rem
      padding .1rem
    .item-info
      padding .1rem
      flex 1
      min-width 0
      .item-title
        line-height .54rem
        font-size .32rem
        ellipse()
      .item-desc
        line-height .4rem
        color:#ccc
        ellipse()
      .item-button
        background #ff9300
        padding 0 .2rem
        border-radius .06rem
        margin-top .3rem
        color #fff
        font-size .25rem

</style>



