<template>
  <div class="container" @click="handleGallaryClick">
     <div class="wrapper">
       <swiper :options="swiperOption">
         <swiper-slide v-for="(item,index) of imgs" :key="index"><img class="gallary-img" :src="item" /></swiper-slide>
         <!--  下面是轮播图的小圆点  -->
       <div class="swiper-pagination"  slot="pagination">
         </div>
       </swiper>
     </div>
  </div>
</template>

<script>
    export default {
        name: "CommonGallary",
        props:{
          imgs:{//所用的图片由外部传入
            type:Array,
            default(){
              return []
            }
          }
        }, data(){
          return{
            swiperOption:{
              pagination:'.swiper-pagination',
              paginationType:'fraction',
              observeParents:true,//监听到父级元素的dom有变化的时候会自我刷新一次
              observer:true

            }
          }
        },
      methods:{
          handleGallaryClick(){
            this.$emit('close')
          }
      }
    }
</script>

<style scoped lang="stylus">
 .container>>>.swiper-container
    overflow inherit
 .container
   display flex
   flex-direction column
   justify-content center
   z-index 99
   position fixed
   left 0
   right 0
   top 0
   bottom 0
   background #000
   .wrapper
     width 100%
     height 0
     padding-bottom 100%
     .gallary-img
       width 100%
     .swiper-pagination
        color #fff
        bottom -1rem

</style>



