<template>
 <div>
<div class="banner" @click="handleBannerClick">
  <img class="banner-img" :src="bannerImg"/>
  <div class="banner-info">
    <div class="banner-title">
      {{sightName}}
    </div>
    <div class="banner-number"> <span class="iconfont banner-icon">&#xe617;</span>{{this.bannerImg.length}}</div>
  </div>
</div>
   <fade-animation>
    <common-gallary :imgs="bannerImgs" v-show="showGallary" @close="handleGallaryClose"></common-gallary>
   </fade-animation>
 </div>
</template>

<script>
  import CommonGallary from 'common/gallary/Gallary'
  import FadeAnimation from 'common/fade/Fade'
    export default {
        name: "DetailBanner",
        components:{
          CommonGallary,
          FadeAnimation
        },
      props:{
        sightName:String,
        bannerImg:String,
        bannerImgs:Array
      },
      data(){
          return {
            showGallary: false,
          }
      },
      methods:{
          handleBannerClick(){
            this.showGallary = true
          },
        handleGallaryClose(){
            this.showGallary=false
        }
      }
    }
</script>

<style scoped lang="stylus">
 .banner
    position relative
    overflow hidden
    height 0
    padding-bottom 55%
    .banner-img
       width 100%
    .banner-info
       display flex
       position absolute
       left 0
       right 0
       bottom 0
       line-height .6rem
       color #fff
       background-image linear-gradient(top,rgba(0,0,0,0),rgba(0,0,0,1))
       .banner-title
          flex 1
          padding 0 .2rem
          font-size .32rem
       .banner-number
          height  .32rem
          margin-top .14rem
          padding 0 .4rem
          line-height .4rem
          border-radius .2rem
          background  rgba(0,0,0,.8rem)
          font-size .24rem
          .banner-icon
            font-size .24rem
</style>



