<template>
  <div class="wrapper">
    <swiper :options="swiperOption" v-if="showSwiper">
      <swiper-slide v-for="item of list" :key="item.id"><img class="swiper-img" :src="item.imgUrl" /></swiper-slide>
      <!--  下面是轮播图的小圆点  -->
      <div class="swiper-pagination"  slot="pagination">
      </div>
    </swiper>
  </div>
</template>

<script>
    export default {
      name: "HomeSwiper",
      props:{
        list:Array
      },
      data (){
        return {
          swiperOption:{
            pagination:'.swiper-pagination',
            loop:true//支持循环轮播
          }
        }

      },
      computed:{
        showSwiper(){
          return this.list.length
        }
      }
    }
</script>

<style scoped lang="stylus">
  .wrapper >>> .swiper-pagination-bullet-active//样式穿透，小圆点的颜色
     background:#fff
  .wrapper
    overflow:hidden
    width:100%
    height:0
    padding-bottom:31.25%
    .swiper-img
         width:100%
         height:2rem
</style>



